#about{
    min-height: 100vh;

    flex-wrap: wrap;
 
    
  
}

.title {
    
    height: 100px;
    color: #fefcfb;
    background-color:  #0a1128;
    display: flex;
    align-items: center;
    justify-items: center;
    font-size: 20pt;
}

.title h3{
    margin-left: 25pt;
    font-size: 1.5em;
    letter-spacing: 4pt;
  
}

.title-second {
    margin-top: 45px;
    height: 100px;
    color: #fefcfb;


    align-items: center;
    justify-items: center;
    font-size: 20pt;
    
}


.title-second h3{
    margin-left: 25pt;
    font-size: 1.5em;
    letter-spacing: 4pt;
  
  
}

.about-content {
   
   
    padding-top: 50px;
   
    flex-wrap: wrap;
   
}

.about-stacks{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    padding: 2px 2px 2px 2px;
    margin-bottom: 30px;
    color: #fefcfb;
    font-size: 1.5em;

    @media screen and (max-width: 768px) {
        grid-template-columns: 1fr 1fr ;
    }
   
    
}


#about-text{

    flex-wrap: wrap;
    color: #fefcfb;
    font-size: 1.5em;
    padding: 12px 12px 12px 12px;
    
}


