.col__2 img{
    
     border-radius: 100%;

    width: 280px;
    height: 320Px;
  
    border: 4px solid #034078;
  
}




#intro {


    /* background-color: #001f54; */
    min-height: 100vh;
    width: 100%;
    display: inline-flex;
    flex-flow: column wrap;
    margin-top: 10%;
    
 
}

.intro-hello{
   
    text-align: left;
      color: #71D4EF;
      font-size: 20px;
      margin-top: 20%;
      margin-left: 30%;
      letter-spacing: 1pt;
      animation: fadeIn ease 3s;
      -webkit-animation: fadeIn ease 3s;
      -moz-animation: fadeIn ease 3s;
      -o-animation: fadeIn ease 3s;
      -ms-animation: fadeIn ease 3s;
  }

.intro-name{

   
    color: #fefcfb;
   
    margin-top: 4%;
    letter-spacing: 7pt;
    animation: fadeIn ease 3s;
    -webkit-animation: fadeIn ease 3s;
    -moz-animation: fadeIn ease 3s;
    -o-animation: fadeIn ease 3s;
    -ms-animation: fadeIn ease 3s;
}

.intro-name h1{
    margin-left: 20pt;
    font-size: 60px;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
}

.intro-bio{

    font-size: 25px;
    padding: 30px;
    color: #71D4EF;
   
    animation: fadeIn ease 7s;
    -webkit-animation: fadeIn ease 7s;
    -moz-animation: fadeIn ease 7s;
    -o-animation: fadeIn ease 7s;
    -ms-animation: fadeIn ease 7s;

}



.intro-tagline{
    
    margin-top: 4%;
    color: #fefcfb;
    font-size: 30px;
    animation: fadeIn ease 5s;
    -webkit-animation: fadeIn ease 5s;
    -moz-animation: fadeIn ease 5s;
    -o-animation: fadeIn ease 5s;
    -ms-animation: fadeIn ease 5s;
}




#contact{
    min-height: 100vh;
    width: 100%;
    display: inline-flex;
    flex-flow: column wrap;
   
  
}

.contact-title h3{
    margin-left: 25pt;
    font-size: 1.5em;
    letter-spacing: 4pt;
}



.contact-content {
    margin: 80px 110px 20px 110px;
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
}

.socials a {
   
    color: #fefcfb ;
    width: 120px;
    height: 120px;
    margin: 60px;
    font-size: 4em;
    
    

}


.socials a:hover{
    /* opacity: 30%; */
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all 0.3s linear;
    color: #71D4EF;
   
  
 } 


#project{
    min-height: 100vh;
    width: 100%;
    flex-flow: column wrap;

    

  
}

.projects-container{
  
    flex-wrap: wrap;
    /* display: inline-flex; */
    display: flex;
    margin-top: 50px;
   
    justify-content: space-around;
    /* justify-content: center; */
    margin-bottom: 50px;
   
}

.project-container{
     display: grid;
     
 
}


.project-container img{
    
   
    width: 300px;
    height: 350Px;
    margin-top: 20px;
    
    border: 4px solid #0a1128;
    /* padding: 3px; */

    opacity: 1;
    transition: opacity 500ms ease-in;

    align-items: center;
    
    
  }
#experience{
    min-height: 100vh;
    width: 100%;
    display: inline-flex;
    flex-flow: column wrap;
   
   
}


.experience-Tab{
   
   margin-left: 50px;
    margin-top: 100px;
    color: #fefcfb;
   
}

.experience-resume{
    margin-top: 50px;
    
    /* margin-top: 149px; */
    color: #fefcfb;
    font-size: 50px;
}



